import React, { memo } from 'react';

const HabitInput = memo((props) => {
  const inputRef = React.createRef();

  const handleAddItem = (e) => {
    e.preventDefault();
    const itemName = inputRef.current.value;
    if (itemName === '') return;
    props.addHabits(itemName);
    inputRef.current.value = '';
  };
  return (
    <div className='habit__input'>
      <form onSubmit={handleAddItem} className='input__form'>
        <input
          ref={inputRef}
          name='item'
          type='text'
          placeholder='item'
          className='input__additem'
        />
        <button className='input__addbtn'>Add Item</button>
      </form>
    </div>
  );
});

export default HabitInput;
