import React, { memo } from 'react';

const Habit = memo((props) => {
  const increaseCount = () => {
    props.increaseCount(props.item);
  };

  const decreaseCount = () => {
    props.decreaseCount(props.item);
  };

  const deleteItem = () => {
    props.deleteItem(props.item);
  };
  const {
    item: { title, count },
  } = props;
  return (
    <li className='habit__comp'>
      <span className='habit-name'>{title}</span>
      <span className='habit-count'>{count}</span>
      <button onClick={increaseCount} className='habit-button habit-increase'>
        <i className='fas fa-plus-square'></i>
      </button>
      <button onClick={decreaseCount} className='habit-button habit-decrease'>
        <i className='fas fa-minus-square'></i>
      </button>
      <button className='habit-button habit-delete' onClick={deleteItem}>
        <i className='fas fa-trash'></i>
      </button>
    </li>
  );
});

export default Habit;
