import './App.css';
import React, { memo, useState } from 'react';
import Habits from './components/Habits';
import HabitTitle from './components/HabitTitle';
import HabitInput from './components/HabitInput';
import HabitReset from './components/HabitReset';

const App = memo(() => {
  const [habits, sethabits] = useState([]);
  const [tracks, setTracks] = useState(0);

  const habitTrack = (habits) => {
    let count = 0;
    if (typeof habits !== 'object') return;
    habits.forEach((e) => {
      if (e.count > 0) count++;
    });
    return count;
  };

  const resetCount = () => {
    const newhabits = [...habits].map((i) => {
      if (i.count === 0) return i;
      else {
        const h = { ...i };
        h.count = 0;
        return h;
      }
    });
    const newtracks = habitTrack(habits);
    sethabits(newhabits);
    setTracks(newtracks);
  };

  const increaseCount = (item) => {
    const newhabits = [...habits].map((i) => {
      if (item.title === i.title) {
        const h = { ...i };
        h.count++;
        return h;
      } else return i;
    });
    const newtracks = habitTrack(newhabits);
    sethabits(newhabits);
    setTracks(newtracks);
  };

  const decreaseCount = (item) => {
    if (item.count <= 0) return;
    const newhabits = [...habits].map((i) => {
      if (item.title === i.title) {
        const h = { ...i };
        h.count--;
        return h;
      } else return i;
    });
    const newtracks = habitTrack(newhabits);
    sethabits(newhabits);
    setTracks(newtracks);
  };

  const deleteItem = (item) => {
    const newhabits = [...habits].filter((i) => {
      return i !== item;
    });
    const newtracks = habitTrack(newhabits);
    sethabits(newhabits);
    setTracks(newtracks);
  };

  const addHabits = (title) => {
    const newhabits = [...habits];
    const habit = {
      title,
      count: 0,
    };
    newhabits.push(habit);
    sethabits(newhabits);
  };
  return (
    <div className='App'>
      <HabitTitle tracks={tracks} />
      <HabitInput addHabits={addHabits} />
      <Habits
        habits={habits}
        increaseCount={increaseCount}
        decreaseCount={decreaseCount}
        deleteItem={deleteItem}
      />
      <HabitReset resetCount={resetCount} />
    </div>
  );
});
export default App;
